import React, { Component } from 'react';
import PropTypes from 'prop-types';
import cx from 'classnames';

import AnimationComponent from '../Animation';

export default class Header extends Component {
  static propTypes = {
    Animation: PropTypes.any.isRequired,

    theme: PropTypes.any.isRequired,
    classes: PropTypes.any.isRequired,

    animate: PropTypes.bool,
    show: PropTypes.bool,
    animation: PropTypes.object,

    /**
     * It uses the `deploy` player.
     */
    sounds: PropTypes.object,

    /**
     * If function, receives the animation status object.
     */
    children: PropTypes.any,

    className: PropTypes.any
  };

  static defaultProps = {
    Animation: AnimationComponent,
    sounds: {},
    show: true
  };

  componentDidMount () {
    const { animate, show, players } = this.props;
    if (animate && show) {
      players.deploy && players.deploy.play();
    }
  }

  componentDidUpdate (prevProps) {
    const { animate, show, players } = this.props;
    if (animate && prevProps.show !== show) {
      players.deploy && players.deploy.play();
    }
  }

  componentWillUnmount () {
    const { animate, players } = this.props;
    if (animate) {
      players.deploy && players.deploy.stop();
    }
  }

  render () {
    const {
      theme,
      classes,
      Animation,
      animation,
      sounds,
      animate,
      show,
      className,
      children,
      ...etc
    } = this.props;
    const cls = cx(classes.root, className);

    return (
      <Animation
        animate={animate}
        show={show}
        timeout={theme.animTime}
        {...animation}
      >
        {anim => (
          <header className={cx(cls, classes[anim.status])} {...etc}>
            <div className={classes.children}>
              {typeof children === 'function' ? children(anim) : children}
            </div>
            <div className={classes.separator} />
          </header>
        )}
      </Animation>
    );
  }
}
